.App {
  background-color: blueviolet;
  padding-left: 20%;
  height: 100%;
  display: flex;
  flex-direction: column;
  min-height: 100%;
  }
  
  .menu {
    display: inline-block;
    background-color: rgb(90, 143, 123);
    margin: 5px;
    padding: 20pt;
    border-radius: 20pt;
    height: 90%;
    position: fixed; 
    top: 0; 
    left: 0;
    bottom: 0;
  }
  
  .list {
    padding: 5pt;
    list-style-type: none;
    font-size: 16pt;
    text-decoration: none;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    text-align: center; 
  }
  
  li {
    padding: 5pt;
  }

  .icon {
    display: inline;
    padding: 20px;
  }

  img {
    width: 50px;
  }
